	module.exports = {"data":{"post":{"html":"<p>The tech project from hell: the German transport ministry today announced the <a title=\"Expatica\" href=\"http://www.expatica.com/source/site_article.asp?subchannel_id=52&story_id=4751\">cancellation of its contract with Toll Collect</a> to build a satellite-based toll and tracking system for trucks. The system had been scheduled to go into operation in August 2003, less than a year after the project was awarded to the consortium led by Daimler Chrysler and Deutsche Telekom. After several catastrophic failures during live testing, Toll Collect is now unable to commit to any start date. The agreement finally fell apart over liability for lost revenue of over 180 million Euros per month.</p>","fields":{"title":"German Engineering","date":"17 Feb 2004","url":"https://papascott.de/2004-02-17-german-engineering/"}}},"pathContext":{"slug":"/2004-02-17-german-engineering/","prev":{"fields":{"title":"Tobias Schwarz, A Fistful of Euros: Reforming Germany is not just hard. It is harder","date":"16 Feb 2004","slug":"/2004-02-16-tobias-schwarz-a-fistful-of-euros-reforming-germany-is-not-just-hard-it-is-harder/"}},"next":{"fields":{"title":"Davids Medienkritik: German-American friendship in Huntsville, Alabama","date":"18 Feb 2004","slug":"/2004-02-18-davids-medienkritik-german-american-friendship-in-huntsville-alabama/"}}}}

